import React from "react"

import { CourseIntroductionPageProps } from "./CourseIntroductionPage.types"
import CourseIntroCard from "../CourseIntroCard"
import "./CourseIntroductionPage.css"
import { useAppSelector } from "../../redux/hooks"
import { selectCourse } from "../../redux/CourseInfoSlice"
import NavPillMaker from "../NavPillMaker"

const CourseIntroductionPage: React.FC<CourseIntroductionPageProps> = ({
  courseId,
}) => {
  const course = useAppSelector(state => selectCourse(state, courseId))
  {
    /* TODO: convert this to error page */
    /* TODO: This will show even when data is loading. handle this.  */
  }
  if (course === null) {
    return <div>ERROR for course: {courseId}</div>
  }

  let { img_url, title, description, syllabus_info } = course
  console.log("syll", course, syllabus_info)
  return (
    <div
      data-testid="CourseIntroductionPage"
      className="CourseIntroductionPage"
    >
      <h1>{title}</h1>
      <div className="CourseHero">
        <h4>{description}</h4>
        <img src={img_url}></img>
      </div>
      <NavPillMaker>
        {syllabus_info.map(syllabus => {
          /* convert it to a Component (more like high order component) */
          return (
            <CourseIntroCard
              standard={syllabus.standard}
              courseId={courseId}
              label={{
                text: "Class " + syllabus.standard,
                id: syllabus.standard,
              }}
            />
          )
        })}
      </NavPillMaker>
      <button> Enroll Now </button>
    </div>
  )
}

export default CourseIntroductionPage
