import React, { ReactNode, ReactChild, useState } from "react"

import { NavPillMakerProps } from "./NavPillMaker.types"
import "./NavPillMaker.css"

const NavPillMaker: React.FC<NavPillMakerProps> = ({ children }) => {
  if (!Array.isArray(children)) {
    return <div>ERROR: NavPillMaker expects an array of children</div>
  }
  const labelChildren = children.map(child => {
    if (React.isValidElement(child)) {
      let obj: any = {};
      if (child && child.props && child.props.label)
      obj[child.props.label.id] = child
      return obj
    }
    else {
      return <text>{typeof child}</text>
    }
  })
  console.log(labelChildren)
  return (
    <div data-testid="NavPillMaker" className="NavPillMaker">
      <ul>
        {Array.isArray(children) &&
          children?.map((child: ReactNode) => {
            if (React.isValidElement(child)) return <li>{child.props.label}</li>
            else {
              return <text>{typeof child}</text>
            }
          })}
      </ul>
      {children}
    </div>
  )
}

export default NavPillMaker
