import React from "react"

import { SyllabusPageProps } from "./SyllabusPage.types"
import "./SyllabusPage.css"
import { useAppSelector } from "../../redux/hooks"
import { selectSyllabus } from "../../redux/CourseInfoSlice"

const SyllabusPage: React.FC<SyllabusPageProps> = ({ courseId, standard }) => {
  const syllabus = useAppSelector(state =>
    selectSyllabus(state, courseId, standard)
  )
  if (syllabus === null) {
    return (
      <div>
        ERROR for course: {courseId} {standard}
      </div>
    )
  }
  return (
    <div data-testid="SyllabusPage" className="SyllabusPage">
      <h1>{courseId}</h1>
      <h2>{standard}</h2>
      {/* should I add learningObjectives and Outcomes again here. */}
      <div className="moduleHolder">
        {syllabus.modules.map(module => (
          <div className="CourseModule" key={module.id}>
            <h3>{module.title}</h3>
            <p>{module.description}</p>
          </div>
        ))}
      </div>
      <button> Enroll Now </button>
    </div>
  )
}

export default SyllabusPage
