import React from "react"

import { CourseIntroCardProps } from "./CourseIntroCard.types"
import "./CourseIntroCard.css"
import { useAppSelector } from "../../redux/hooks"
import { selectSyllabus } from "../../redux/CourseInfoSlice"
import {Link} from "react-router-dom"

const CourseIntroCard: React.FC<CourseIntroCardProps> = ({
  standard,
  courseId,
  label
}) => {
  const syllabus = useAppSelector(state =>
    selectSyllabus(state, courseId, standard)
  )
  console.log("syllabus", syllabus, label)
  return (
    <div data-testid="CourseIntroCard" className="CourseIntroCard">
      <h3>{standard}</h3>
      <div className="LearningObjectives">
        {/* TODO: convert this to a component and use it for both learningObjectives and leanringOutcomes */}
        <h4>Learning Objectives</h4>
        <ul>
          {syllabus?.learning_objectives.map(item => (
            <li>{item}</li>
          ))}
        </ul>
      </div>
      <div className="LearningOutcomes">
        <h4>Learning Outcomes</h4>
        <ul>
          {syllabus?.learning_outcomes.map(item => (
            <li>{item}</li>
          ))}
        </ul>
      </div>
      <Link to={`/course/${courseId}/${standard}`}>
        <button>View Syllabus</button>
      </Link>
    </div>
  )
}

export default CourseIntroCard
