import React from "react"

import { CourseLPCardProps } from "./CourseLPCard.types"
import "./CourseLPCard.css"
import { Link } from "react-router-dom"
import { selectCourse } from "../../redux/CourseInfoSlice"
import { useAppSelector } from "../../redux/hooks"

const CourseLPCard: React.FC<CourseLPCardProps> = ({ courseId }) => {
  const course = useAppSelector(state => selectCourse(state, courseId))

  if (course === null) {
    return <div>ERROR for course: {courseId}</div>
  }

  let { id, img_url, title, brief } = course

  return (
    <div data-testid="CourseLPCard" className="CourseLPCard">
      <img src={img_url}></img>
      <div className="description">
        <h4>{title}</h4>
        <p>{brief}</p>
        <Link to={`/course/${id}`}>
          <button>Enroll Now</button> {/* convert it to a button component */}
        </Link>
      </div>
    </div>
  )
}

export default CourseLPCard
